@import '../variables';
@import '../mixins/mixins';
@import '../themes';

@mixin rtl {
    @at-root [dir="rtl"] #{&} {
        @content
    }
}


$color-selected: $seashell;

.ng-select {

    &.ng-select-opened {    
        > .ng-select-container {
            background: $white;
            border-color: $alto;
        }
    }
    &.ng-select-focused {
        .ng-select-container {
            box-shadow: 0 0 0 0.2rem rgba($black, 0.15);
        }
    }
    &.ng-select-disabled {
        > .ng-select-container {
            background-color: #e9ecef;
            .ng-arrow-wrapper {
                background-color: #e9ecef;
            }
        }
    }
    .ng-has-value .ng-placeholder {
        display: none;
    }
    .ng-select-container {
        background-color: $white;
        border: 1px solid $alto;
        min-height: 38px;
        align-items: center;
	    border-radius: 0.25rem;
        .ng-value-container {
            align-items: center;
            padding-left: 12px;
            @include rtl {
                padding-right: 12px;
                padding-left: 0
            }
            .ng-placeholder {
                color: $black;
            }
        }
    }
    &.ng-select-single {
        .ng-select-container {
            height: 36px;
            .ng-value-container {
                .ng-input {
                    top: 5px;
                    left: 0;
                    padding-left: 10px;
                    padding-right: 50px;
                    @include rtl {
                        padding-right: 10px;
                        padding-left: 50px;
                    }
                }
            }
        }
    }
    &.ng-select-multiple {
        &.ng-select-disabled {
            > .ng-select-container .ng-value-container .ng-value {
                background-color: $secondary;
                .ng-value-label {
                    padding: 0 5px;
                }
            }
        }
        .ng-select-container {
            .ng-value-container {
                padding-top: 5px;
                padding-left: 7px;
                @include rtl {
                    padding-right: 7px;
                    padding-left: 0
                }
                .ng-value {
                    color: $black;
                    font-size: 1rem;
                    margin-bottom: 5px;
                    background-color: $color-selected;
                    border: 0;
                    margin-right: 5px;
                    @include rtl {
                        margin-right: 0;
                        margin-left: 5px;
                    }
                    &.ng-value-disabled {
                        background-color: $secondary;
                        .ng-value-label {
                            padding-left: 5px;
                            @include rtl {
                                padding-left: 0;
                                padding-right: 5px;
                            }
                        }
                    }
                    .ng-value-label {
                        display: inline-block;
                        padding: 0 8px 0 4px;
                    }
                    .ng-value-icon {
                        display: inline-block;
                        padding: 0 8px;
                        &:hover {
                            background-color: $gray;
                            color: $white;
                        }
                        &.left {
                            border-right: 0;
                            @include rtl {
                                border-left: 1px solid $alto;
                                border-right: none;
                            }
                        }
                        &.right {
                            border-left: 0;
                            @include rtl {
                                border-left: 0;
                                border-right: 1px solid $alto;
                            }
                        }
                    }
                }
                .ng-input {
                    padding: 0 0 3px 3px;
                    @include rtl {
                        padding: 0 3px 3px 0;
                    }
                }
                .ng-placeholder {
                    top: 5px;
                    padding-bottom: 5px;
                    padding-left: 5px;
                    @include rtl {
                        padding-right: 3px;
                        padding-left: 0;
                    }
                }
            }
        }
    }
    .ng-has-value {
        .ng-clear-wrapper {
            color: $dark-gray;
            width: 20px;
            &:hover .ng-clear {
                color: $primary;
            }
        }
    }
    .ng-spinner-zone {
        padding: 5px 5px 0 0;

        @include rtl {
            padding: 5px 0 0 5px;
        }
    }
    .ng-arrow-wrapper {
        background: $white url('data:image/svg+xml,%3C%3Fxml version=\'1.0\' encoding=\'utf-8\'%3F%3E%3Csvg version=\'1.1\' id=\'Layer_1\' xmlns=\'http://www.w3.org/2000/svg\' xmlns:xlink=\'http://www.w3.org/1999/xlink\' x=\'0px\' y=\'0px\' viewBox=\'0 0 1792 1792\' style=\'enable-background:new 0 0 1792 1792;\' xml:space=\'preserve\'%3E%3Cstyle type=\'text/css\'%3E .st0%7Bfill:%23767676;%7D%0A%3C/style%3E%3Cpath class=\'st0\' d=\'M1395,736c0,8.7-3.3,16.3-10,23l-466,466c-6.7,6.7-14.3,10-23,10s-16.3-3.3-23-10L407,759 c-6.7-6.7-10-14.3-10-23s3.3-16.3,10-23l50-50c6.7-6.7,14.3-10,23-10s16.3,3.3,23,10l393,393l393-393c6.7-6.7,14.3-10,23-10 s16.3,3.3,23,10l50,50C1391.7,719.7,1395,727.3,1395,736z\'/%3E%3C/svg%3E') no-repeat right 0.5rem center;
        background-size: 20px 20px;
        position: relative;
        width: 25px;

        @include rtl {
            padding-left: 5px;
            padding-right: 0;
        }
    }
    &.is-invalid {
        & ~ .invalid-feedback {
            display: block;
        }
        .ng-select-container {
            border-color: $danger;
        }
    }
}


.ng-dropdown-panel {
    background-color: $white;
    border: 1px solid $alto;
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
    left: 0;
    &.ng-select-bottom {
        top: 100%;
        border-top-color: $alto;
        margin-top: 2px;
    }
    &.ng-select-top {
        bottom: 100%;
        border-bottom-color: $alto;
        margin-bottom: -1px;
    }
    .ng-dropdown-header {
        border-bottom: 1px solid $alto;
        padding: 5px 7px;
    }
    .ng-dropdown-footer {
        border-top: 1px solid $dark-gray;
        padding: 5px 7px;
    }
    .ng-dropdown-panel-items {
        margin-bottom: 1px;
        margin-top: 4px;
        .ng-optgroup {
            user-select: none;
            padding: 0 14px;
            font-family: $font-family-book;
            font-size: 15px;
            line-height: 28px;
            margin-bottom: 3px;
            color: $dark-gray;
            cursor: pointer;
            &.ng-option-disabled {
                cursor: default;
            }
            &.ng-option-marked {
                background-color: $secondary;
            }
            &.ng-option-selected {
                background-color: $color-selected;
                font-weight: 600;
            }
        }
        .ng-option {
            background-color: $white;
            color: transparent;
            padding: 1px 24px;
            .dropdown-item {
                background-color: transparent;
                padding: 3px 0;
            }
            .text {
                font-family: $font-family-book;
            }
            .text-desc {
                font-size: 14px;
            }
            &.ng-option-selected {
                color: $black;
                background-color: $color-selected;
                .ng-option-label {
                    font-family: $font-family-book;
                }
            }
            &.ng-option-marked {
                background-color: $color-selected;
                color: $black;
            }
            &.ng-option-disabled {
                color: $gray;
            }
            &.ng-option-child {
                padding: 4px 14px;
                line-height:19px;
                font-size: 15px;
                @include rtl {
                    padding-right: 22px;
                    padding-left: 0;
                }
            }
            .ng-tag-label {
                font-size: 80%;
                font-weight: 400;
                padding-right: 5px;
                @include rtl {
                    padding-left: 5px;
                    padding-right: 0;
                }
            }
        }
    }


    @include rtl {
        direction: rtl;
        text-align: right;
    }
}


